@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}

:root {
    --black: #333;
    --black2: #333;
    --white: #fff;
    --white2: #fff;
}

.dark {
    --black: #fff;
    --black2: #444;
    --white: #333;
    --white2: #444;
}

body {
    overflow-x: hidden;
    width: 100%;
}

.container {
    position: relative;
    width: 100%;
}

.navigation {
    position: fixed;
    width: 300px;
    height: 100vh;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    background: #fff;
    background: var(--white2);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: width 0.5s left 0.5s;
}

.dark .navigation {
    border-right: 1px solid rgba(0, 0, 0, 0.5);
}

.navigation.active {
    width: 0;
}

.dark h2 {
    color: #fff;
}

.navigation ul {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 40px;
}

.navigation ul li {
    position: relative;
    width: 100%;
    list-style: none;
}

.navigation ul li a {
    position: relative;
    display: block;
    width: 100%;
    margin: 10px 0;
    white-space: normal;
    display: flex;
    text-transform: uppercase;
    text-decoration: none;
    color: #333;
    color: var(--black);
    font-weight: 500;
    letter-spacing: 2px;
    opacity: 0.5;
}

.navigation ul li a:hover {
    opacity: 1;
}

.main {
    position: absolute;
    width: calc(100% - 300px);
    left: 300px;
    background: #fff;
    background: var(--white);
    min-height: 100vh;
    transition: width 0.5s, left 0.5s;
}

.main.active {
    width: 100%;
    left: 0;
}

.main .topbar {
    position: fixed;
    top: 0;
    width: calc(100% - 300px);
    left: 300px;
    background: #fff;
    background: var(--white2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    height: 60px;
    padding: 0 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 10000;
    transition: width 0.5s, left 0.5s;
}

.dark .main .topbar {
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.main .topbar.active {
    width: 100%;
    left: 0;
}

.main .topbar .logo {
    color: #333;
    color: var(--black);
    font-weight: 700;
    text-decoration: none;
    font-size: 1.4em;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.main .topbar .logo:hover {
    cursor: pointer;
}

.main .topbar .toggle {
    position: relative;
    width: 60px;
    height: 60px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: #333;
    color: var(--black);
}

section {
    position: relative;
    min-height: 100vh;
}

.banner {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
}

.banner .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.banner .imgBx {
    position: relative;
    width: 300px;
    height: 300px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 20px;
}

.banner .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

h3 {
    position: relative;
    font-size: 1.4em;
    color: #333;
    color: var(--black);
    letter-spacing: 2px;
    text-transform: uppercase;
}

p {
    color: #777;
}

.dark p {
    color: #333;
    color: var(--black);
}

.btn {
    position: relative;
    padding: 10px 40px;
    display: inline-block;
    background: #333;
    background: var(--black2);
    color: #fff;
    font-weight: 400;
    letter-spacing: 1px;
    margin-top: 10px;
    text-decoration: none;
    border: none;
}

.btn:hover {
    cursor: pointer;
}

.dark .btn:hover {
    background: #2196f3;
}

.socialMedia {
    position: absolute;
    bottom: 20px;
    display: flex;
}

.socialMedia li {
    list-style: none;
}

.socialMedia li a {
    display: inline-block;
    width: 40px;
    height: 40px;
    background: rgba(0, 0, 0, 0.05);
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #333;
    color: var(--black);
    font-size: 1.2em;
}

.dark .socialMedia li a:hover {
    background: #2196f3;
}

.socialMedia li a:hover {
    background: #333;
    background: var(--black2);
    color: #fff;
}

.adjuts {
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 0 40px;
    padding-top: 80px;
    background: #fff;
    background: var(--white);
    color: #333;
    color: var(--black);
    display: inline-block;
}

.title {
    width: 100%;
    text-align: center;
}

.title h2 {
    position: relative;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 1.2em;
    margin-bottom: 30px;
}

.title h2::before {
    content: '';
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: -10px;
    width: 50px;
    height: 1px;
    background: #333;
    background: var(--black);
}

.about .content {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
}

.about .content .textBox {
    width: 49%;
    min-width: 49%;
    text-align: justify;
    padding: 0 30px;
}

.about .content .imgBx {
    margin-right: 20px;
}

.about .content .imgBx img {
    max-width: 100%;
}

.services .content {
    grid-template-columns: repeat(2, 1fr);
}

.services .content, .project .content {
    display: grid;
    grid-gap: 20px;
    margin: 40px 0;
}

.services .content .serviceBox {
    border: 1px solid rgba(0, 0, 0, 0.05);
    padding: 50px 20px;
    display: flex;
    background: #fff;
    background: var(--white2);
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.dark .services .content .serviceBox:hover {
    background: #2196f3;
}

.services .content .serviceBox img {
    max-width: 50px;
}

.dark .services .content .serviceBox img {
    -webkit-filter: invert(1);
            filter: invert(1);
}

.services .content .serviceBox h2 {
    font-size: 1.1em;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: 600;
    color: #333;
    color: var(--black);
    text-align: center;
    letter-spacing: 2px;
    text-transform: uppercase;
}

.services .content .serviceBox p {
    text-align: center;
}

.project .content {
    grid-template-columns: repeat(3, 1fr);
}

.project .content .workBx {
    position: relative;
    width: 100%;
    height: 300px;
    overflow: hidden;
}

.project .content .workBx .imgBx {
    position: relative;
    width: 100%;
    height: 100%;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: 0.5s ease-in-out;
}

.project .content .workBx:hover .imgBx {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.project .content .workBx .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project .content .workBx .textBx {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #333;
    background: var(--black);
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: 0.5s ease-in-out;
}

.dark .project .content .workBx .textBx {
    background: #444;
}

.project .content .workBx:hover .textBx {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.project .content .workBx .textBx h3 {
    color: #fff;
    font-size: 1em;
}

.testimonials .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    margin: 40px 0;
}

.testimonials .content .testimonialsBox {
    background: #fff;
    background: var(--white2);
    border: 1px solid rgba(0, 0, 0, 0.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    padding: 20px;
}

.dark .testimonials .content .testimonialsBox:hover {
    background: #2196f3;
}

.testimonials .content .testimonialsBox .imgBx {
    position: relative;
    width: 97px;
    height: 130px;
    margin-bottom: 10px;
    margin: auto;
}

.testimonials .content .testimonialsBox .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: auto;
    height: 130px;
}

.testimonials .content .testimonialsBox p {
    font-style: italic;
    padding: 5px 15px;
}

.testimonials .content .testimonialsBox h4 {
    text-align: right;
    margin-top: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 1em;
    color: #333;
    color: var(--black);
    line-height: 1.2em;
    font-weight: 600;
    border-right: 4px solid #333;
    border-right: 4px solid var(--black);
    padding-right: 10px;
}

.testimonials .content .testimonialsBox h4 span {
    text-transform: none;
    text-transform: initial;
    color: #aaa;
    letter-spacing: 1px;
    font-weight: 300;
    font-size: 0.7em;
}

.dark .contact .contactForm .row2 input[type="submit"]:hover {
    background: #2196f3;
}

.copyright {
    position: relative;
    bottom: 0;
    padding: 10px 40px;
    text-align: center;
}

/* Dark and Light switch */
.themeSwitch {
    position: fixed;
    bottom: 40px;
    left: 40px;
    width: 30px;
    height: 30px;
    background: #333;
    background: var(--black);
    color: #fff;
    color: var(--white);
    cursor: pointer;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.themeSwitch::after {
    content: 'Modo Noche';
    position: absolute;
    left: 40px;
    font-size: 0.7em;
    color: #333;
    color: var(--black);
    white-space: nowrap;
    font-weight: 300;
    letter-spacing: 1px;
    pointer-events: none;
}

.dark .themeSwitch::after {
    content: 'Modo día';
}

::-webkit-scrollbar {
    background: #fff;
    background: var(--white);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #333;
    background: var(--black);
}

/* Responsive */
@media(min-width: 900px) {
    .testimonials .content .testimonialsBox {
        display: flex;
        justify-content: space-between;
    }

    .testimonials .content .testimonialsBox .imgBx {
        width: 100%;
        margin: 0;
    }

    .testimonials .content .testimonialsBox .imgBx img {
        width: 100%;
        height: auto;
        object-fit: cover;
    }
}

@media(max-width: 992px) {
    .themeSwitch {
        left: 20px;
    }

    .navigation {
        left: -100%;
    }

    .navigation.active {
        left: 0;
        width: 100%;
    }

    .main {
        width: 100%;
        left: 0;
    }

    .main.active {
        display: none;
    }

    .main .topbar {
        width: 100%;
        left: 0;
        padding: 0 20px;
    }

    .navigation ul {
        padding: 20px 20px;
    }
    
    .banner .imgBx {
        width: 250px;
        height: 250px;
    }

    .banner h3, .banner p {
        text-align: center;
    }

    .adjuts {
        padding: 0 20px;
        padding-top: 80px;
    }

    .about .content {
        flex-direction: column;
    }

    .about .content .textBox {
        width: 100%;
        min-width: 100%;
        padding: 0 20px;
    }

    .about .content .imgBx {
        margin-left: 0;
        margin-top: 20px;
    }

    .services .content {
        grid-template-columns: repeat(2, 1fr);
    }

    .project .content {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media(max-width: 767px) {
    .services .content {
        grid-template-columns: repeat(1, 1fr);
    }

    .project .content {
        grid-template-columns: repeat(1, 1fr);
    }

    .testimonials .content {
        grid-template-columns: repeat(1, 1fr);
    }

    .contact .contactForm .row {
        grid-template-columns: repeat(1, 1fr);
    }

    .contact {
        margin-bottom: 100px;
    }

    .copyright {
        padding: 10px 20px;
    }
}
.hojavida {
    margin-top: 20px;
}

embed {
    width: 100%;
    min-height: 100vh;
    margin: 0;
    padding: 0;
}

button.return {
    width: 100%;
    padding: 5px;
    text-transform: uppercase;
    background: rgb(37, 40, 43);
    border: none;
    color: white;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

button.return:hover {
    cursor: pointer;
}

.main.active {
    display: block;
}
